<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">PreLoader</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">
                    预加载指示器
                </div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDemo()">有提示文字</p>
                <p class="btn" @click.stop="onClickDemo2()">无提示文字</p>
            </li>
            <li>
                <h3>Function：</h3>
<pre class="JS">
// 显示动画
var content = '提示内容';//可选
mobileModal.showPreLoader(content);

// 关闭动画
mobileModal.hidePreLoader();

</pre>
            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    引入文件：</br>
                    'mobileModal/mobileModal.css'</br>
                    'mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
        name: 'toast',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                mobileModal.showPreLoader('提示内容');
                setTimeout(function () {
                    mobileModal.hidePreLoader();
                }, 1000);
            },

            onClickDemo2(){
                mobileModal.showPreLoader();
                setTimeout(function () {
                    mobileModal.hidePreLoader();
                }, 1000);
            }
        }
    }
</script>

<style scoped>
    /*app.vue 模块中已引入*/
    /*@import "../../lib/mobileModal/mobileModal.css";*/
</style>


